.main-page {
    margin: 10px;
    width: 45%;
    border-radius: 45px;
    min-width: 280px;
    background-color: #ffffffef
}

.ant-slider {
    margin: 13px 6px 10px
}

.ant-tooltip {
    font-size: 13px
}

.ant-tooltip-inner {
    min-width: 20px;
    min-height: 20px;
    width: 20px;
    height: 20px;
    padding: 1px 6px
}

.ant-input-affix-wrapper .ant-input-prefix, .ant-input-affix-wrapper .ant-input-suffix {
    line-height: 1;
    left: 9px;
    transform: translateY(-35%);
    background-color: #ffffff00
}

.ant-select-selection__rendered {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 16px;
}

.ant-select {
    font: 14px bolder;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.ant-input-affix-wrapper .ant-input-prefix, .ant-input-affix-wrapper .ant-input-suffix {
    color: #000000;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 15px;
}

.ant-select-selection-selected-value {
    width: 100%;
}

.ant-btn-lg {
    font-size: 30px;
}

.ant-layout-footer {
    color: rgb(255, 255, 255);
    background: rgba(0, 0, 0, 0);
    padding: 5px;
    margin: 5px;
    height: 10%;
    width: auto;
}

.ant-layout-header {
    margin: 0px;
    padding: 0px;
    height: 47px;
    background: rgba(0, 0, 0, 0) url(http://stephanviranyi.com/wordpress/wp-content/uploads/2013/04/abstract_Logo.png) no-repeat center;
    background-size: 20%;
}

body {
    background-image: url(https://massivelyop.com/wp-content/uploads/2016/05/albion2.jpg);
    background-repeat: no-repeat;
    /* background-position: center; */
    background-size: cover;
    background-size: 2895px;
    /* url(https://massivelyop.com/wp-content/uploads/2016/05/albion2.jpg) no-repeat; */
}

.ant-select-lg .ant-select-selection--single {
    background-color: #ffffff;
    text-align: left;
    color: #000000;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.ant-input[disabled] {
    background-color: #ffffff;
    color: #041ca0;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font: 18px bolder
}

.ant-input {
    background-color: #ffffff;
    color: #041ca0;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font: 18px bolder
}

.ant-input::placeholder {
    color: #000000
}